<!-- subpkg/webview/webview.vue -->
<template>
	<view class="webview-page">
		<!-- Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
		<view class="custom-navbar" :style="{ paddingTop: statusBarHeight + 'px' }">
			<view class="navbar-content">
				<view class="navbar-left" @click="goBack">
					<text class="back-icon">‚Üê</text>
				</view>
				<view class="navbar-center">
					<text class="navbar-title">{{ pageTitle }}</text>
				</view>
				<view class="navbar-right">
					<text class="refresh-icon" @click="refreshWebview">üîÑ</text>
					<text class="more-icon" @click="showActionSheet">‚ãØ</text>
				</view>
			</view>
		</view>
		
		<!-- Âä†ËΩΩËøõÂ∫¶Êù° -->
		<view class="progress-bar" v-if="isLoading">
			<view class="progress-fill" :style="{ width: progress + '%' }"></view>
		</view>
		
		<!-- Webview ÂÆπÂô® -->
		<web-view 
			class="webview-container"
			:src="webviewUrl"
			@load="onLoad"
			@error="onError"
			@loading="onLoading"
			@loaded="onLoaded"
		></web-view>
		
		<!-- ÈîôËØØÁä∂ÊÄÅ -->
		<view class="error-container" v-if="showError">
			<view class="error-content">
				<text class="error-icon">üòû</text>
				<text class="error-title">È°µÈù¢Âä†ËΩΩÂ§±Ë¥•</text>
				<text class="error-message">{{ errorMessage }}</text>
				<view class="error-actions">
					<view class="error-btn retry" @click="retryLoad">
						<text>ÈáçÊñ∞Âä†ËΩΩ</text>
					</view>
					<view class="error-btn back" @click="goBack">
						<text>ËøîÂõû‰∏äÈ°µ</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				webviewUrl: '',
				pageTitle: '',
				originalUrl: '',
				statusBarHeight: 0,
				isLoading: true,
				progress: 0,
				showError: false,
				errorMessage: '',
				progressTimer: null
			}
		},
		
		onLoad(options) {
			// Ëé∑Âèñ‰º†ÂÖ•ÁöÑ URL ÂíåÊ†áÈ¢ò
			this.originalUrl = decodeURIComponent(options.url || '')
			this.pageTitle = decodeURIComponent(options.title || 'Âä†ËΩΩ‰∏≠...')
			this.webviewUrl = this.originalUrl
			
			console.log('Webview Âä†ËΩΩ:', this.webviewUrl)
			
			// Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
			this.getSystemInfo()
			
			// ÂºÄÂßãÂä†ËΩΩËøõÂ∫¶Ê®°Êãü
			this.startProgressAnimation()
		},
		
		onUnload() {
			// Ê∏ÖÁêÜÂÆöÊó∂Âô®
			if (this.progressTimer) {
				clearInterval(this.progressTimer)
			}
		},
		
		methods: {
			// Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØ
			getSystemInfo() {
				uni.getSystemInfo({
					success: (res) => {
						this.statusBarHeight = res.statusBarHeight || 20
					}
				})
			},
			
			// ÂºÄÂßãËøõÂ∫¶Âä®Áîª
			startProgressAnimation() {
				this.progress = 0
				this.progressTimer = setInterval(() => {
					if (this.progress < 90) {
						this.progress += Math.random() * 10
					}
				}, 200)
			},
			
			// ÂÆåÊàêËøõÂ∫¶Âä®Áîª
			completeProgress() {
				if (this.progressTimer) {
					clearInterval(this.progressTimer)
				}
				this.progress = 100
				setTimeout(() => {
					this.isLoading = false
				}, 300)
			},
			
			// webview ÂºÄÂßãÂä†ËΩΩ
			onLoading() {
				console.log('Webview ÂºÄÂßãÂä†ËΩΩ')
				this.isLoading = true
				this.showError = false
				this.startProgressAnimation()
			},
			
			// webview Âä†ËΩΩÊàêÂäü
			onLoad() {
				console.log('Webview Âä†ËΩΩÊàêÂäü')
				this.completeProgress()
				this.showError = false
			},
			
			// webview Âä†ËΩΩÂÆåÊàê
			onLoaded() {
				console.log('Webview Âä†ËΩΩÂÆåÊàê')
				this.completeProgress()
			},
			
			// webview Âä†ËΩΩÈîôËØØ
			onError(event) {
				console.error('Webview Âä†ËΩΩÈîôËØØ:', event)
				this.isLoading = false
				this.showError = true
				this.errorMessage = this.getErrorMessage(event)
				
				if (this.progressTimer) {
					clearInterval(this.progressTimer)
				}
			},
			
			// Ëé∑ÂèñÈîôËØØ‰ø°ÊÅØ
			getErrorMessage(event) {
				// Ê†πÊçÆ‰∏çÂêåÁöÑÈîôËØØÁ±ªÂûãËøîÂõûÁî®Êà∑ÂèãÂ•ΩÁöÑÈîôËØØ‰ø°ÊÅØ
				if (event.detail && event.detail.errMsg) {
					const errMsg = event.detail.errMsg.toLowerCase()
					if (errMsg.includes('network')) {
						return 'ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËÆæÁΩÆ'
					} else if (errMsg.includes('timeout')) {
						return 'È°µÈù¢Âä†ËΩΩË∂ÖÊó∂ÔºåËØ∑ÈáçËØï'
					} else if (errMsg.includes('ssl') || errMsg.includes('certificate')) {
						return 'ÂÆâÂÖ®ËØÅ‰π¶È™åËØÅÂ§±Ë¥•'
					}
				}
				return 'È°µÈù¢Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúÊàñÈáçËØï'
			},
			
			// ËøîÂõû‰∏ä‰∏ÄÈ°µ
			goBack() {
				uni.navigateBack({
					fail: () => {
						// Â¶ÇÊûúÊó†Ê≥ïËøîÂõûÔºåÂàôË∑≥ËΩ¨Âà∞È¶ñÈ°µ
						uni.switchTab({
							url: '/pages/index/index'
						})
					}
				})
			},
			
			// Âà∑Êñ∞È°µÈù¢
			refreshWebview() {
				this.isLoading = true
				this.showError = false
				this.webviewUrl = ''
				
				// Âª∂ËøüÈáçÊñ∞ËÆæÁΩÆ URLÔºåÁ°Æ‰øù webview ÈáçÊñ∞Âä†ËΩΩ
				setTimeout(() => {
					this.webviewUrl = this.originalUrl
					this.startProgressAnimation()
				}, 100)
				
				uni.showToast({
					title: 'Ê≠£Âú®Âà∑Êñ∞...',
					icon: 'loading',
					duration: 1000
				})
			},
			
			// ÈáçÊñ∞Âä†ËΩΩ
			retryLoad() {
				this.refreshWebview()
			},
			
			// ÊòæÁ§∫Êìç‰ΩúËèúÂçï
			showActionSheet() {
				uni.showActionSheet({
					itemList: ['Âà∑Êñ∞È°µÈù¢', 'Â§çÂà∂ÈìæÊé•', 'Âú®ÊµèËßàÂô®‰∏≠ÊâìÂºÄ'],
					success: (res) => {
						switch (res.tapIndex) {
							case 0:
								this.refreshWebview()
								break
							case 1:
								this.copyUrl()
								break
							case 2:
								this.openInBrowser()
								break
						}
					}
				})
			},
			
			// Â§çÂà∂ÈìæÊé•
			copyUrl() {
				uni.setClipboardData({
					data: this.originalUrl,
					success: () => {
						uni.showToast({
							title: 'ÈìæÊé•Â∑≤Â§çÂà∂',
							icon: 'success'
						})
					}
				})
			},
			
			// Âú®ÊµèËßàÂô®‰∏≠ÊâìÂºÄ
			openInBrowser() {
				uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'Âç≥Â∞ÜÂ§çÂà∂ÈìæÊé•Âà∞Ââ™Ë¥¥ÊùøÔºåËØ∑Âú®ÊµèËßàÂô®‰∏≠Á≤òË¥¥ÊâìÂºÄ',
					success: (res) => {
						if (res.confirm) {
							uni.setClipboardData({
								data: this.originalUrl,
								success: () => {
									uni.showToast({
										title: 'ÈìæÊé•Â∑≤Â§çÂà∂ÔºåËØ∑Âú®ÊµèËßàÂô®‰∏≠ÊâìÂºÄ',
										icon: 'none',
										duration: 3000
									})
								}
							})
						}
					}
				})
			}
		}
	}
</script>

<style lang="scss">
.webview-page {
	height: 100vh;
	background-color: #ffffff;
	display: flex;
	flex-direction: column;
}

// Ëá™ÂÆö‰πâÂØºËà™Ê†è
.custom-navbar {
	background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
	box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.1);
	z-index: 1000;
	
	.navbar-content {
		height: 88rpx;
		display: flex;
		align-items: center;
		padding: 0 24rpx;
		
		.navbar-left {
			width: 80rpx;
			display: flex;
			align-items: center;
			
			.back-icon {
				font-size: 36rpx;
				color: #ffffff;
				font-weight: bold;
			}
		}
		
		.navbar-center {
			flex: 1;
			text-align: center;
			
			.navbar-title {
				font-size: 32rpx;
				color: #ffffff;
				font-weight: 500;
				max-width: 400rpx;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
		}
		
		.navbar-right {
			width: 120rpx;
			display: flex;
			justify-content: flex-end;
			gap: 24rpx;
			
			.refresh-icon,
			.more-icon {
				font-size: 32rpx;
				color: #ffffff;
				padding: 8rpx;
			}
		}
	}
}

// ËøõÂ∫¶Êù°
.progress-bar {
	height: 4rpx;
	background-color: rgba(255, 255, 255, 0.3);
	position: relative;
	overflow: hidden;
	
	.progress-fill {
		height: 100%;
		background: linear-gradient(90deg, #4facfe 0%, #00f2fe 100%);
		transition: width 0.3s ease;
		position: absolute;
		left: 0;
		top: 0;
	}
}

// Webview ÂÆπÂô®
.webview-container {
	flex: 1;
	width: 100%;
}

// ÈîôËØØÁä∂ÊÄÅ
.error-container {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ffffff;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 999;
	
	.error-content {
		text-align: center;
		padding: 40rpx;
		
		.error-icon {
			font-size: 120rpx;
			margin-bottom: 32rpx;
			display: block;
		}
		
		.error-title {
			font-size: 36rpx;
			color: #333333;
			font-weight: 500;
			margin-bottom: 16rpx;
			display: block;
		}
		
		.error-message {
			font-size: 28rpx;
			color: #666666;
			line-height: 1.5;
			margin-bottom: 48rpx;
			display: block;
		}
		
		.error-actions {
			display: flex;
			gap: 24rpx;
			justify-content: center;
			
			.error-btn {
				padding: 20rpx 40rpx;
				border-radius: 12rpx;
				font-size: 28rpx;
				text-align: center;
				transition: all 0.3s;
				
				&.retry {
					background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
					color: #ffffff;
					
					&:active {
						opacity: 0.8;
					}
				}
				
				&.back {
					background-color: #f5f5f5;
					color: #666666;
					
					&:active {
						background-color: #e8e8e8;
					}
				}
			}
		}
	}
}

// ÈÄÇÈÖç‰∏çÂêåÂπ≥Âè∞
/* #ifdef MP-WEIXIN */
.custom-navbar {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 1000;
}

.webview-container {
	margin-top: var(--navbar-height);
}
/* #endif */

// ÊöóÈªëÊ®°ÂºèÈÄÇÈÖç
@media (prefers-color-scheme: dark) {
	.webview-page {
		background-color: #1f1f1f;
	}
	
	.error-container {
		background-color: #1f1f1f;
		
		.error-title {
			color: #ffffff;
		}
		
		.error-message {
			color: #cccccc;
		}
		
		.error-btn.back {
			background-color: #333333;
			color: #cccccc;
		}
	}
}
</style>